import React, { useCallback, useContext } from 'react';
import { IconList } from 'core/src/icons';

import { EditorContext } from '../EditorContext';
import { DropdownButton, DropdownItem } from '../Dropdown';
import Group from '../Group';
import { ControlButtonProps } from './types';

const FormatListButton: React.FC<ControlButtonProps> = ({ hidden }) => {
  const { editor, disabled } = useContext(EditorContext);

  const ulHandler = useCallback((): void => {
    editor?.chain().focus().toggleBulletList().run();
  }, [editor]);

  const olHandler = useCallback((): void => {
    editor?.chain().focus().toggleOrderedList().run();
  }, [editor]);

  return (
    <Group hidden={ hidden }>
      <DropdownButton
        label={ <IconList /> }
        disabled={ disabled }
        alignRight
      >
        <DropdownItem onClick={ ulHandler } data-test-action="ul"><span>Маркированный список</span></DropdownItem>
        <DropdownItem onClick={ olHandler } data-test-action="ol"><span>Нумерованный список</span></DropdownItem>
      </DropdownButton>
    </Group>
  );
};

FormatListButton.displayName = 'FormatListButton';

export default React.memo(FormatListButton);
